.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Align the content closer to the top */
  min-height: 100vh;
  background-color: #4d8688; /* Updated background color */
  color: #ecf0f1;
  font-family: 'Arial', sans-serif;
  padding: 20px;
}

h1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  margin-top: 10px; /* Reduce gap to top of screen */
}

.word {
  display: flex;
  gap: 5px;
  margin-bottom: 10px;
}

.word span {
  display: inline-block;
  background-color: #ecf0f1;
  color: #2c3e50;
  padding: 4px;
  border-radius: 5px;
  font-weight: bold;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  text-align: center;
  font-size: 1.2rem;
  border: 2px solid #2c3e50;
  transform: scaleX(0.9); /* Make the blocks 20% narrower */
}

.clock-container {
  position: relative;
  width: 350px;
  height: 350px;
  border: 4px solid #ecf0f1;
  border-radius: 50%;
  background-color: #34495e;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.number {
  position: absolute;
  font-size: 1.25rem;
  font-weight: bold;
  color: #ecf0f1;
}

.clock-hand {
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform-origin: bottom center;
}
